import React, { Component } from 'react';
import TourDetailCommon from './TourDetailCommon';
import TourDetailInfo from './TourDetailInfo';
import TourDetailGallery from './TourDetailGallery';
//import { BrowserRouter, Route, Switch, Link, Redirect} from 'react-router-dom';
import './TourDetail.css';
import { connect } from 'react-redux';
import * as action from 'action';
import $ from 'jquery';
import Indicator from 'component/UI/Indicator';

class TourDetail extends Component {
  constructor(props) {
    super(props);

    if (this.props.history !== null) {
      this.props.setHistory(this.props.history);
    }
    
    //const buffer = this.props.location.pathname.split('/');
    //const currentPath = buffer[buffer.length - 1];
    this.pageList = ['common', 'info', 'gallery'];
    //var idx = this.pageList.indexOf(currentPath);
    var idx = 0;
    this.state = {
      index: idx,
      page: 'common',
      oldPage: '',
      left: '0px',
      indicatorLeft: '0px'
    };
    //window.scrollTo(0,0);
  }

  getPosition(index){
    return `-${index * 100}vw`;
  }

  getPageName(index) {
    return this.pageList[index];
  }

  movePage(index) {
    if (index > -1 && index < this.pageList.length) {
      this.setState({
        ...this.state,
        index: index,
        /*left: this.getPosition(index),*/
        page: this.getPageName(index),
        indicatorLeft: `${100/3*index}vw`
      });
      var body = $('#dv-tour-detail-body');
      body.stop();
      body.animate( { scrollLeft: window.innerWidth * index} , 500);
      //document.getElementById('dv-tour-detail-body').scrollTo(window.innerWidth * index, 0);
    }
  }

  toLeft (distance) {
    //console.log('to left');
    if (distance > 150) {
      this.movePage(this.state.index + 1);
    }
  }

  toRight (distance) {
    //console.log('to right');
    if (distance > 150) {
      this.movePage(this.state.index - 1);
    }
  }

  componentDidMount(){
    const buffer = this.props.location.pathname.split('/');
    const currentPath = buffer[buffer.length - 1];
    var idx = this.pageList.indexOf(currentPath);
    if (idx !== this.state.index) {
      this.movePage(idx);
    }
    /*window.initTabs('#dv-tour-detail ul.tabs', {
      swipeable : true,
      responsiveThreshold : '100vw',
    });*/
    /*window.setSwipeCallBack(
      this.toLeft.bind(this), 
      this.toRight.bind(this), 
      'dv-tour-detail');*/
  }

  onClick (id) {
    var idx = this.state.idx;
    for(var i = 1; i < 4; ++i) {
      if ($(`#detail-${i}`).attr('class').indexOf('active') > -1) {
        idx = i;
        break;
      }
    }
    console.log(idx);
    if (idx !== this.state.idx) {
      this.setState({
        ...this.state,
        idx: idx,
      })
    }
    //if (this.idx !== )
    /*var linkData = {
        pathname: `${url}/${path}`,
        state: {
          ...this.props.location.state,
          path: path,
        }
    }
    this.props.history.replace(linkData);*/
  }

  renderTab(url, index, path, name){
    var page = this.state.page ? this.state.page : 'common';
    return (
      <span className={page === path ? 'on' : ''} 
        onClick={ () => { this.movePage(index); }}>
        <div>
          {name}
        </div>
      </span>);
  }

  renderIndicator (){
    var style = {left: this.state.indicatorLeft};
    return (<div className='indicator transition-default'  > 
      <div id='dv-indicator-bar' style={style} className={'transition-fast'}/>
      <div id='dv-indicator-bar' style={style} className={'transition-x2'}/>
      <div id='dv-indicator-bar' style={style} className={'transition-default'}/>
    </div>);
  }
  
  render() {
    var { url } = this.props.match;
    if (url[url.length - 1] === '/') {
      url = url.substr(0, url.length - 1);
    }

    return (
      <div id='dv-tour-detail'>
        <div id='dv-tour-detail-tab'>
          {this.renderTab(url, 0, 'common', '공통정보')}
          <div className='line'/>
          {this.renderTab(url, 1, 'info', '소개정보')}
          <div className='line'/>
          {this.renderTab(url, 2, 'gallery', '추가이미지')}
          {this.renderIndicator()}
        </div>
        <div id='dv-tour-detail-body' className='transition-default' style={ { left: this.state.left } }>
          <TourDetailCommon history={this.props.history} match={this.props.match} 
            toLeft={(dist)=> { this.toLeft(dist); }} 
            toRight={(dist)=> { this.toRight(dist); }} />
          <TourDetailInfo history={this.props.history} match={this.props.match} 
            toLeft={(dist)=> { this.toLeft(dist); }} 
            toRight={(dist)=> { this.toRight(dist); }} />
          <TourDetailGallery history={this.props.history} match={this.props.match} 
            toLeft={(dist)=> { this.toLeft(dist); }} 
            toRight={(dist)=> { this.toRight(dist); }} />
        </div>        
      </div>);
  }
}

const mapStateToProps = (state) => {
  return {
      //user: state.auth.user
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) }
      //setPage: (page, header, footer) => { dispatch(action.setPage(page, header, footer)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(TourDetail);